.ba-team {
  padding-bottom: 10px;
}

.ba-team__header {
  padding: 50px 10px 0;
}

.ba-team__icon {
  background: url("../img/icon-team.png") 50% no-repeat;
  width: 60px;
  height: 60px;
}

.ba-team__title {
  color: #665f60;
}

.ba-team__member {
  width: 380px;
  min-height: 485px;
  padding: 130px 15px 0;
}

.ba-team__member__wrapper {
  width: 100%;
  background-color: #fff;
  position: relative;
}

.ba-team__member-photo {
  position: absolute;
  border-radius: 50%;
  border: 13px solid #ebebeb;
  transform: translate(-50%, -50%);
  left: 50%;
}

.ba-team__member-info {
  margin-top: 165px;
  text-align: center;
}

.ba-team__member-name {
  color: #9f9acf;
  font-family: "Proxima Nova", sans-serif;
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 34px;
}

.ba-team__member-occupation {
  font-size: 24px;
  font-style: italic;
}

.ba-team__member-social-links {
  width: 100%;
  height: 70px;
  background-color: #9f9acf;
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ba-team-slider {
  margin: 0 auto;
  max-width: 1140px;
  padding: 126px 15px 0;
  position: relative;
  top: -90px;
}

/* Arrows */
.ba-team-slider .slick-prev,
.ba-team-slider .slick-next {
  top: 65%;
}

.ba-team-slider .slick-prev {
  background: url("../img/interface---arrow-left--dark.svg") 50% no-repeat;
  left: -7%;
}

.ba-team-slider .slick-next {
  background: url("../img/interface---arrow-right--dark.svg") 50% no-repeat;
  right: -7%;
}

.ba-team-slider .slick-prev:hover {
  background: rgba(51, 255, 153, 0.5) url("../img/interface---arrow-left--dark.svg") 50% no-repeat;
}

.ba-team-slider .slick-next:hover {
  background: rgba(51, 255, 153, 0.5) url("../img/interface---arrow-right--dark.svg") 50% no-repeat;
}

@media all and (max-width: 1160px) {
  .ba-team-slider .slick-next {
    right: 0;
  }
  .ba-team-slider .slick-prev {
    left: 0;
  }
}
